.Existingpartnerdetails-card-style{
    display: flex;
    flex-direction: column;
    flex:1;
    background-color: rgb(255, 255, 255);
    border-radius: 3px;
    min-height: 75vh;
    padding: 15px;
    margin-right: 25px;
    width: 400px;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 20%), 0 1px 1px 0 rgb(0 0 0 / 14%), 0 2px 1px -1px rgb(0 0 0 / 12%);
    
}

